.home-main {
  background-image: linear-gradient(to top, #5ee7df 0%, #b490ca 100%);
  /* background-image: linear-gradient(to top, #cb2d3e 0%, #30122d 100%); */
  height: 100%;
  width: 100%;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
